import m, { ClassComponent } from 'mithril'
import Black from '../pages/layout/black'
class Login implements ClassComponent {
  view(
    this: m._NoLifecycle<this & {}>,
    vnode: m.Vnode<{}, m._NoLifecycle<this & {}>>
  ): void | m.Children {
    console.log(vnode)
    return (
      <Black >
        <div class="flex flex-col ">
          <div className="text-2xl">
            Blade
          </div>
          <div className="text-1xl mt-5">
            
          </div>
          <div className="mt-10 ">
            <form className="" >
              <span class="text-gray-700">:</span>
              <input type="email" class="form-input mx-5 px-4 py-3 rounded-full" placeholder="" ></input>
              <span class="text-gray-700">:</span>
              <input type="password" class="form-input mx-5 px-4 py-3 rounded-full" placeholder=""></input>
              <input type="submit" class="form-input mx-5 px-4 py-3 rounded-full bg-primary text-white" value=""></input>
            </form>  
          </div>
        </div>
      </Black>
    )
  }
}
export default Login
